import React from "react";
import { DateObj, Props } from "dayzed";

import { spacingMap } from "../styleguide";

import { StaticDatePickerProps } from "./DatePicker.types";
import DatePickerPopover, { DatePickerPopoverProps } from "./DatePickerPopover";
import StaticDatepicker from "./StaticDatePicker";

type ExtendedProps = Pick<DatePickerPopoverProps, "closeOnDateSelected" | "trigger" | "label" |
	"dateFormat" | "placeholder" | "$zIndex" | "inputLeftIcon" | "inputRightIcon">;

type OmittedStaticDatePickerProps = Omit<StaticDatePickerProps, "dayzedProps"> & {
	dayzedProps: Omit<Props, "onDateSelected">
};

export type DatePickerProps = ExtendedProps & {
	/**
	 * @description omits onDateSelected as that function is used under the hood
	 */
	staticDatePickerProps: OmittedStaticDatePickerProps;
	onDateChange: (date?: Date | Date[]) => void;
};

const DatePicker = ({
	trigger,
	staticDatePickerProps,
	closeOnDateSelected,
	onDateChange,
	label,
	dateFormat,
	placeholder,
	$zIndex,
	inputLeftIcon,
	inputRightIcon,
}: DatePickerProps) => {
	const handleOnDateSelected = (
		{
			date,
		}: DateObj,
	) => {
		onDateChange(date);
	};

	return (
		<DatePickerPopover
			dataQA={`${staticDatePickerProps.dataQA}-popover`}
			trigger={trigger}
			datesSelected={staticDatePickerProps.dayzedProps.selected}
			closeOnDateSelected={closeOnDateSelected}
			onDateChange={onDateChange}
			label={label}
			dateFormat={dateFormat}
			placeholder={placeholder}
			$zIndex={$zIndex}
			inputRightIcon={inputRightIcon}
			inputLeftIcon={inputLeftIcon}
		>
			<StaticDatepicker
				{...staticDatePickerProps}
				dayzedProps={{
					...staticDatePickerProps.dayzedProps,
					onDateSelected: handleOnDateSelected,
				}}
				m={spacingMap.md}
			/>
		</DatePickerPopover>
	);
};

export default DatePicker;
